import issue from '@/fetch/modules/issue';

//
const install = Vue => {
    // 
  if (install.installed) { return; }
  install.installed = true;

  Object.defineProperties(Vue.prototype, {
        //  Vue  $api 
    $api: {
      get() {
        return {
          issue,
        }
      }
    }
  })
}
export default install
